import {useState, useEffect} from 'react';
import {Box, Typography } from '@mui/material';
import { fetchFromAPI } from '../utils/fetchFromAPI';
import {Videos} from './'; //we are already in components folder
import { useParams } from 'react-router-dom';

const SearchFeed = () => {

  const[videos, setVideos] = useState([]);
  const{searchTerm} = useParams(); //will be populated with whatever is in url after /search


  //once we get query we pass it to useEffect so it gets updated when we search something
  useEffect(() => {
    fetchFromAPI(`search?part=snippet&q=${searchTerm}`)
    .then((data) => setVideos(data.items))
  }, [searchTerm]); 

  return (
    <Box p={2} sx ={{overflowY: "auto" , height: "90vh", flex: 2}}>
      <Typography variant="h4" fontWeight="bold" mb="2" sx={{color : "white"}}>
        Search Results for: <span style={{color: "#F31503"}}> {searchTerm} </span> videos
      </Typography>
      <Videos videos={videos}/>
    </Box>
  )
}

export default SearchFeed